.container {
  width: 80%;
  background-color: white;
  margin: 30px auto;
}
.container .tabs {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  cursor: pointer;
  position: static;
  top: 0;
  left: 0;
}
.container .tabs .tab {
  padding: 0 13px;
  height: 34px;
  font-size: 14px;
  line-height: 34px;
  font-weight: bold;
  text-align: center;
  color: #666666;
  border-color: #e6e6e6;
  border-top-style: solid;
  border-left-style: solid;
  border-width: 1px;
  background-color: #f9f9f9;
}
.container .tabs .tab:last-child {
  border-right-style: solid;
}
.container .tabs .select {
  color: #f5bc00;
  background-color: white;
  border-bottom-color: white;
  border-bottom-style: solid;
}
.container .topTabs {
  width: 80%;
  display: -ms-flexbox;
  display: flex;
  cursor: pointer;
  position: fixed;
  top: 0;
  left: 50%;
  -webkit-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  transform: translateX(-50%);
  background-color: white;
}
.container .topTabs .tab {
  padding: 0 13px;
  height: 50px;
  font-size: 14px;
  line-height: 50px;
  font-weight: bold;
  text-align: center;
  color: #666666;
}
.container .topTabs .select {
  color: #f5bc00;
}
.container .productList {
  width: 100%;
}
.container .productList .item {
  width: 100%;
  height: 30px;
  font-size: 12px;
  line-height: 30px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border-bottom: 0.5px solid #f0f0f0;
  padding: 0 20px;
  display: -ms-flexbox;
  display: flex;
  cursor: pointer;
}
.container .productList .item:hover {
  background-color: #fff3e3;
}
.container .productList .item .name {
  -ms-flex: 1 1;
  flex: 1 1;
  text-align: left;
}
.container .productList .item .fullName {
  -ms-flex: 1 1;
  flex: 1 1;
  text-align: left;
}
.container .productList .item .price {
  -ms-flex: 1 1;
  flex: 1 1;
  text-align: right;
}
.container .productList .item .low {
  -ms-flex: 1 1;
  flex: 1 1;
  text-align: right;
}
.container .productList .item .high {
  -ms-flex: 1 1;
  flex: 1 1;
  text-align: right;
}
.container .productList .item .volumn {
  -ms-flex: 1 1;
  flex: 1 1;
  text-align: right;
}
.container .productList .item:first-child {
  border-top: 0.5px solid #f0f0f0;
}



