.container {
  width: 80%;
  background-color: white;
  margin: 30px auto;
}
.container .tools {
  position: absolute;
  top: 5px;
  right: 5px;
}
.container .connectBtn {
  width: 100px;
  height: 40px;
  font-size: 20px;
  line-height: 40px;
  text-align: center;
  border: 0.5px solid black;
  border-radius: 5px;
  cursor: pointer;
}
.container .orderList {
  width: 280px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  border-width: 1px;
  border-style: solid;
  border-color: #e6e6e6;
  -webkit-border-image: initial;
  -o-border-image: initial;
  border-image: initial;
  padding: 0px;
  margin: 5px 5px 0px 0px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-direction: column;
  flex-direction: column;
  cursor: pointer;
}
.container .orderList .banner {
  width: 278px;
