  display: flex;
  font-size: 12px;
  color: #999;
  font-family: Arial, Microsoft YaHei, sans-serif !important;
  font-weight: normal;
}
.container .orderList .currentPrice {
  width: 100%;
  height: 30px;
  background-color: #f7f7f7;
}
.container .orderList .asks,
.container .orderList .bids {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  height: 344px;
  position: relative;
  width: 278px;
  overflow: hidden;
  direction: ltr;
}
.container .orderList .asks .list,
.container .orderList .bids .list {
  width: 278px;
  overflow: hidden;
  position: absolute;
}
.container .orderList .asks .list .item,
.container .orderList .bids .list .item {
  width: 100%;
  height: 20px;
  display: -ms-flexbox;
  display: flex;
  font-family: Arial, Microsoft YaHei, sans-serif !important;
  font-weight: 300;
  font-size: 12px;
  line-height: 20px;
